var lizTable = angular.module('lizTable', []);

lizTable.directive('tableConcept', function () {
	return {
		restrict: 'E',
		templateUrl: '../views/concepts/table.html',
		scope: {
			options: "=",
			title: '@',
			audio: '@',
			instruction: '@',
			description: '@',
			buttonText: '@'
		},
		link: function (scope) {

			var opt = scope.options;

			// Si la descripcin o el ttulo estn, entonces la instruccin va al fondo
			scope.isBottom = scope.title || scope.description;

			scope.head = opt.head;
			scope.body = opt.body;
			scope.buttonText = scope.buttonText;

			scope.$root.isNextEnabled = true;

		}
	};
});
