esp103_.controller('Con1Ctrl', function($scope){
  $scope.activateNext = function () {
    $scope.$root.isNextEnabled = true;
  };
});

esp103_.controller('Act1Ctrl', function($scope, $sce){
  var regex = null, // Regex auxiliary variable
    completedLetters = 0; // Contador

  $scope.letters = [
    {
      letter: "a",
      name: "a",
      title: "avin"
    },
    {
      letter: "b",
      name: "be",
      title: "baln"
    },
    {
      letter: "c",
      name: "ce",
      title: "casa"
    },
    {
      letter: "d",
      name: "de",
      title: "dado"
    },
    {
      letter: "e",
      name: "e",
      title: "elefante"
    },
    {
      letter: "f",
      name: "efe",
      title: "flor"
    },
    {
      letter: "g",
      name: "ge",
      title: "gato"
    },
    {
      letter: "h",
      name: "hache",
      title: "hoja"
    },
    {
      letter: "i",
      name: "i",
      title: "iglesia"
    },
    {
      letter: "j",
      name: "jota",
      title: "jugo"
    },
    {
      letter: "k",
      name: "ka",
      title: "kiosco"
    },
    {
      letter: "l",
      name: "ele",
      title: "limn"
    },
    {
      letter: "m",
      name: "eme",
      title: "manzana"
    },
    {
      letter: "n",
      name: "ene",
      title: "naranja"
    },
    {
      letter: "",
      name: "ee",
      title: "and"
    },
    {
      letter: "o",
      name: "o",
      title: "olla"
    },
    {
      letter: "p",
      name: "pe",
      title: "paleta"
    },
    {
      letter: "q",
      name: "cu",
      title: "queso"
    },
    {
      letter: "r",
      name: "ere",
      title: "ratn"
    },
    {
      letter: "s",
      name: "ese",
      title: "silla"
    },
    {
      letter: "t",
      name: "te",
      title: "tomate"
    },
    {
      letter: "u",
      name: "u",
      title: "uvas"
    },
    {
      letter: "v",
      name: "ve",
      title: "vaca"
    },
    {
      letter: "w",
      name: "ve doble",
      title: "wilson"
    },
    {
      letter: "x",
      name: "equis",
      title: "xilfono"
    },
    {
      letter: "y",
      name: "ye",
      title: "yate"
    },
    {
      letter: "z",
      name: "zeta",
      title: "zanahoria"
    }
  ];

  $scope.selectedLetter = false; // Letra seleccionada en cada momento

  // Aadimos la propiedad src a cada una
  $scope.letters.forEach(function (l) {
    // Recurso de la imagen
    if(l.letter !== '') l.src = l.letter;
    else l.src = 'n_'; // para la 

    regex = new RegExp(l.letter, "g");

    // Modifica el ttulo en base a la letra, aadiendo strong
    l.title = l.title.replace(regex, '<strong>' + l.letter + '</strong>')
  });


  /**
   * Remueve la letra al dar click.
   * @param letter
   */
  $scope.remove = function (letter) {
    $scope.selectedLetter = letter; // selecciona la letra
    letter.hide = true; // oculta

    completedLetters += 1;

    // Fin de la actividad
    if(completedLetters === $scope.letters.length) {
      $scope.$root.isNextEnabled = true;
      $scope.success = true;
    }
  };

  /**
   * filtra el html antes de usarlo de forma segura.
   */
  $scope.sanitize = function (str) {
    return $sce.trustAsHtml(str);
  };

});

esp103_.controller('Act2Ctrl', function($scope){
  $scope.data = {
    items: [
      { letter: 'a' },
      { letter: 'h' },
      { letter: 'c' },
      { letter: 'd' },
      { letter: 'e' },
      { letter: 'f' },
      { letter: 'o' },
      { letter: 'i' },
      { letter: 'u' }
    ],
    itemTemplate: '<div style="font-size: 65px; width: 70px; cursor: move; text-align:center;">{{ item.letter }}</div>',
    showIf: "letter",
    targets: 5,
    chances: 6,
    minRightAnswers: 3,
    pass: function (target) {
      return "aeiou".search(target.letter) >= 0;
    }
  };
});

esp103_.controller('Act3-1Ctrl', function($scope){
  $scope.items = [
    {
      name: "avin",
      letter: "a"
    },
    {
      name: "baln",
      letter: "b"
    },
    {
      name: "casa",
      letter: "c"
    },
    {
      name: "dado",
      letter: "d"
    }
  ];

  $scope.items.forEach(function (item) {
    item.src = item.letter;
  });

  $scope.description = "Las letras del abecedario pueden escribirse de diferentes formas y tamaos, pero su nombre nunca cambia. Arrastra el objeto hacia el cuadro que contiene la letra con la cual comienza su nombre.";

  $scope.data = {
    data: $scope.items,
    chances: 8,
    minRightAnswers: 3,
    randomItems: true,
    randomTargets: true,
    padding: false,
    border: false
  };
});

